import React, {
  Dispatch,
  SetStateAction,
  useEffect,
  useRef,
  useState,
} from 'react'
import { css } from '@emotion/react'
import palette from '../../lib/palette'
import { BiX, BiChevronDown } from 'react-icons/bi'
import { parse } from '@babel/core'

export type SelectProps = {
  label: string
  prefix?: string
  value?: string
  setValue?: Dispatch<SetStateAction<string>>
  isClearable?: boolean
  isMulti?: boolean
} & React.InputHTMLAttributes<HTMLInputElement>

function Select({
  prefix,
  label,
  value,
  setValue,
  isClearable = true,
  isMulti,
  ...rest
}: SelectProps) {
  const inputRef = useRef<HTMLInputElement>(null)
  const [inputSize, setInputSize] = useState(0)
  const [isActive, setIsActive] = useState(false)
  const [multiValue, setMultiValue] = useState<string[] | null>([
    '전국',
    '대전',
    '부산',
    '전국',
    '대전',
    '부산',
    '전국',
    '대전',
    '부산',
    '전국',
    '대전',
    '부산',
    '전국',
    '대전',
    '부산',
  ])

  useEffect(() => {
    setIsActive(false)
    if (value !== '' || value.length !== 0) {
      return setIsActive(true)
    }
  }, [value])

  useEffect(() => {
    if (value === '' || value?.length === 0 || value?.length === undefined) {
      return
    }
    setInputSize(value.length * 2)
  }, [value])

  const isClear = () => {
    if (!setValue) {
      return
    }
    setValue('')
  }

  const onKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (!['ArrowDown', 'ArrowUp', 'Enter'].includes(e.key)) return
    e.preventDefault()
    console.log(e.key)
    switch (e.key) {
      case 'ArrowUp':
        return
      case 'ArrowDown':
        return
      case 'Enter':
        return
    }
  }
  return (
    <div css={block}>
      <div css={base(isActive, multiValue)} {...rest}>
        <label css={labelStyle}>{label}</label>
        <div css={multiCss(isActive, multiValue)}>
          {multiValue?.map((item) => (
            <div css={multiValueCss}>{item}</div>
          ))}
          <input
            size={inputSize}
            maxLength={50}
            css={multiInputCss}
            ref={inputRef}
            value={value}
            onKeyDown={onKeyDown}
          />
        </div>

        {isClearable && value !== '' && (
          <div css={clear} onClick={isClear}>
            <BiX />
          </div>
        )}
        <div css={downArrow(isActive, multiValue)}>
          <BiChevronDown />
        </div>
      </div>
      <div css={selectBlock}>
        <div css={selectItem}>전국</div>
        <div css={selectItem}>서울특별시</div>
        <div css={selectItem}>부산광역시</div>
        <div css={selectItem}>대구광역시</div>
      </div>
    </div>
  )
}

export default Select

const block = css`
  width: 100%;
  position: relative;
`
const base = (isActive: boolean, multiValue: string[] | null) => css`
  border: 1px solid ${palette.primary['grey30']};
  border-radius: 4px;
  min-height: 3rem;
  /* position: relative; */
  display: flex;

  padding-left: 0.5rem;
  padding-right: 0.5rem;

  &:focus-within {
    border: 1px solid ${palette.primary['accentBlue']};
  }

  ${isActive &&
  css`
    label {
      height: auto;
      transform: translate(0, 4px) scale(0.75);
    }
  `}

  ${multiValue &&
  css`
    label {
      height: auto;
      transform: translate(0, 4px) scale(0.75);
    }
  `}
`
const multiCss = (isActive: boolean, multiValue: string[] | null) => css`
  display: flex;
  flex: 1 1 0%;
  flex-wrap: wrap;
  overflow: hidden;
  align-items: center;

  ${isActive &&
  css`
    div {
      margin-left: 10px;
    }
    margin-top: 14px;
  `}

  ${multiValue &&
  css`
    div {
      margin-left: 10px;
    }
    margin-top: 14px;
  `}
`
const multiValueCss = css`
  background: ${palette.primary['grey30']};
  border: 1px solid ${palette.primary['lightBlue']};
  min-height: 24px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 6px 3px;
  padding-left: 8px;
  padding-right: 8px;
  font-size: 0.75rem;
  /* &:first-child {
    margin-left: 10px;
  } */
`

const multiInputCss = css`
  border: 0;
  outline: 0;
  font-size: 1rem;
  background: #fff;
  color: ${palette.primary['notBlack']};
  margin-left: 8px;
`

const labelStyle = css`
  padding-left: 13px;
  font-size: 1rem;

  display: flex;
  align-items: center;
  color: ${palette.primary['grey70']};
  pointer-events: none;
  position: absolute;
  transform: translate(0, 12px) scale(1);
  transform-origin: top left;
  transition: all 0.2s ease-out;
`
const clear = css`
  display: flex;
  align-items: center;
  font-size: 1.5rem;
  padding-left: 0.5rem;
  cursor: pointer;
  color: ${palette.primary['grey70']};
  &:hover {
    color: ${palette.primary['notBlack']};
  }
`
const downArrow = (isActive: boolean, multiValue: string[] | null) => css`
  display: flex;
  align-items: center;
  font-size: 1.7rem;
  padding-left: 0.5rem;
  cursor: pointer;
  color: ${palette.primary['grey70']};
  &:hover {
    color: ${palette.primary['notBlack']};
  }
  ${isActive &&
  css`
    &:before {
      content: '';
      margin-right: 0.5rem;
      width: 1px;
      height: 1.5rem;
      background: ${palette.primary['grey70']};
    }
  `}

  ${multiValue &&
  css`
    &:before {
      content: '';
      margin-right: 0.5rem;
      width: 1px;
      height: 1.5rem;
      background: ${palette.primary['grey70']};
    }
  `}
`

const selectBlock = css`
  width: 100%;
  max-height: 138px;
  border-radius: 4px;

  background: #fff;
  box-shadow: 0 2px 8px 0px rgba(0, 0, 0, 0.2);
  margin-top: -4px;
  padding-top: 4px;
  padding-bottom: 4px;
  margin-bottom: 1rem;
  overflow: hidden;
`
const selectItem = css`
  padding: 8px 18px;
  &:hover {
    background: ${palette.primary['grey30']};
  }
`
